//localStorage   
//sessionStorage  iframe 
// setItem(key, value)  /
// getItem(key)  
// removeItem(key)  
// clear()  
// key(index)  
// length  
//  Object.keys 
//  storage 
const public_Key = 'public_key';
export default {
  getPublicKey(): string {
    const token = localStorage.getItem(public_Key);
    if (token) {
      return '-----BEGIN PUBLIC KEY-----' + token + '-----END PUBLIC KEY-----';
    } else {
      return '';
    }
  },
  setPublicKey(token: string): void {
    return localStorage.setItem(public_Key, token);
  },
  removePublicKey(): void {
    return localStorage.removeItem(public_Key);
  },
  clear(): void {
    return localStorage.clear();
  },
};
