import {
  Module,
  VuexModule,
  Mutation,
  Action,
  getModule,
} from 'vuex-module-decorators';
import store from '@/store';
import ApiService from '@/api/serviceBase';
import { SongStoreVo, SongRequestVo } from '@/store/vo/music-song-vo';
// modulename
// modulevuextrue
//        index.tsfalse
//        
// 
// store
/**
 * API
 * @author hai
 */
@Module({
  name: 'songStore',
  dynamic: true,
  namespaced: true,
  store,
})
class songStore extends VuexModule {
  private songStoreVos: SongStoreVo[] = [];
  /**
   *
   * @param
   */
  @Action({ rawError: true })
  public async exeGetSongsApi(songRequestVo: SongRequestVo): Promise<void> {
    const response = await ApiService.Post('/getSongs', songRequestVo);
    console.log(response);
    this.setSongsStore(response.data);
  }

  /**
   *
   * @param publickey
   */
  @Mutation
  private setSongsStore(songStoreVos: SongStoreVo[]): void {
    this.songStoreVos = songStoreVos;
  }

  public get getSongs(): SongStoreVo[] {
    return this.songStoreVos;
  }
  /**
   *
   * @param
   */
  @Action({ rawError: true })
  public async clear(): Promise<void> {
    this.clearStore();
  }
  /**
   *
   * @param publickey
   */
  @Mutation
  private clearStore(): void {
    this.songStoreVos = [];
  }
}
export const songStoreModule = getModule(songStore);
