import {
  Module,
  VuexModule,
  Mutation,
  Action,
  getModule,
} from 'vuex-module-decorators';
import store from '@/store';
import { loginStoreVo } from '../vo/login-store-vo';
import ApiService from '@/api/serviceBase';
import auth from '@/config/auth';
// modulename
// modulevuextrue
//        index.tsfalse
//        
// 
// store
/**
 * 
 * @author hai
 */
@Module({
  name: 'loginStore',
  dynamic: true,
  namespaced: true,
  store,
})
class loginStore extends VuexModule {
  public name = '';

  /**
   *
   * @param name
   */
  @Action({ rawError: true })
  public async executeLoginApi(loginStoreVo: loginStoreVo): Promise<void> {
    const response = await ApiService.Post('/login', loginStoreVo);
    console.log(response);
    if (response.data.data.errorInfo) {
      console.log(response);
    } else {
      this.setTokenToStore(response.data.data);
    }
  }

  /**
   *
   * @param token
   */
  @Mutation
  private setTokenToStore(token: string) {
    auth.setToken(token);
  }

  public get getName(): string {
    return this.name;
  }
}
export const loginModule = getModule(loginStore);
